@import "../../styles/helpers/_helpers";

.main {
    margin: 200px 0 100px;
    &__inner {

        position: relative;

    }
    &__photo {
        position: absolute;
        content: '';
        top: -115px;
        left: 50%;
        transform: translate3d(-50%, 0, 0);
        border: 7px solid #00bfff;
        border-radius: 50%;
        width: 230px;
        height: 230px;
        background-repeat: no-repeat;
        overflow: hidden;
        img {
            background-color: #00bfff;
            margin-top: -21px;
        }
        @include max-screen(991px) {
            top: -85px;
            width: 160px;
            height: 160px;
            img {
                margin-top: -11px;
            }
        }
    }
    &__aboutMe {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        .title {
            display: flex;
            justify-content: space-between;
            flex-direction: row;
            flex-wrap: nowrap;
            text-transform: uppercase;
            font-family: 'Neuton';
            font-size: 55px;
            font-weight: 400;
            margin-top: 0;
            margin-bottom: 80px;
            @include max-screen(1199px) {
                font-size: 35px;
            }
            @include max-screen(991px) {
                font-size: 30px;
            }
            @include max-screen(767px) {
                margin-top: 40px;
                margin-bottom: 30px;
                flex-direction: column;
                align-items: center;
            }
            
        }
    }
    &__discription {
        text-align: center;
        font-family: 'Open Sans Condensed', sans-serif;
        font-size: 40px;
        max-width: 800px;
        align-self: center;
        letter-spacing: 3px;
        line-height: 1.2;
        margin-bottom: 30px;
        @include max-screen(1199px) {
            font-size: 28px;
        }
        @include max-screen(767px) {
            font-size: 24px;
        }
    }
    &__contacts {
        display: flex;
        justify-content: space-around;
        flex-direction: row nowrap;
    }
    .link-wimg {
        @include max-screen(767px) {
            padding: 0 10px;
            font-size: 24px;
        }
    }
}
