.main {
  margin: 200px 0 100px;
}

.main__inner {
  position: relative;
}

.main__photo {
  position: absolute;
  content: '';
  top: -115px;
  left: 50%;
  transform: translate3d(-50%, 0, 0);
  border: 7px solid #00bfff;
  border-radius: 50%;
  width: 230px;
  height: 230px;
  background-repeat: no-repeat;
  overflow: hidden;
}

.main__photo img {
  background-color: #00bfff;
  margin-top: -21px;
}

.main__aboutMe {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}

.main__aboutMe .title {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: nowrap;
  text-transform: uppercase;
  font-family: 'Neuton';
  font-size: 55px;
  font-weight: 400;
  margin-top: 0;
  margin-bottom: 80px;
}

.main__discription {
  text-align: center;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 40px;
  max-width: 800px;
  align-self: center;
  letter-spacing: 3px;
  line-height: 1.2;
  margin-bottom: 30px;
}

.main__contacts {
  display: flex;
  justify-content: space-around;
  flex-direction: row nowrap;
}

@media only screen and (max-width: 1199px) {
  .main__aboutMe .title {
    font-size: 35px;
  }

  .main__discription {
    font-size: 28px;
  }
}

@media only screen and (max-width: 991px) {
  .main__photo {
    top: -85px;
    width: 160px;
    height: 160px;
  }

  .main__photo img {
    margin-top: -11px;
  }

  .main__aboutMe .title {
    font-size: 30px;
  }
}

@media only screen and (max-width: 767px) {
  .main__aboutMe .title {
    margin-top: 40px;
    margin-bottom: 30px;
    flex-direction: column;
    align-items: center;
  }

  .main__discription {
    font-size: 24px;
  }

  .main .link-wimg {
    padding: 0 10px;
    font-size: 24px;
  }
}